.navbar {
  box-shadow: 0 2px 12px 0 rgb(0 0 0 / 16%);
}
nav.navbar .nav-link, nav.navbar  .navbar-brand {
  color: #344767;
  font-size: .875rem;
  font-weight: 400;
}
.btn.btn-round {
  border-radius: 1.875rem;
}
.bg-gradient-primary {
  background-image: linear-gradient(
310deg
,#7928ca,#ff0080);
}
.navbar .btn-round{
  padding: 0.5rem 2rem;
  color: aliceblue;
  text-transform: uppercase;
  font-size: .75rem;
  font-weight: 700;
}
.navbar .btn-round:hover{
  color: aliceblue;
}
.navbar-light .navbar-toggler {
  border: none;
  font-size: 1.125rem;
}
.navbar .navbar-toggler .navbar-toggler-icon {
  background-image: none;
}
.navbar-toggler .navbar-toggler-icon .navbar-toggler-bar {
  display: block;
  position: relative;
  width: 22px;
  height: 1px;
  border-radius: 1px;
  background: #6c757d;
  transition: all .2s;
  margin: 0 auto;
}
.navbar-toggler .navbar-toggler-icon .navbar-toggler-bar.bar2, .navbar-toggler .navbar-toggler-icon .navbar-toggler-bar.bar3 {
  margin-top: 7px;
}
.navbar-toggler[aria-expanded=true] .navbar-toggler-bar.bar2 {
  opacity: 0;
}
.navbar-toggler[aria-expanded=true] .navbar-toggler-bar.bar1 {
  transform: rotate(45deg);
  transform-origin: 10% 10%;
  margin-top: 4px;
}
.navbar-toggler[aria-expanded=true] .navbar-toggler-bar.bar3 {
  transform: rotate(
-45deg
);
  transform-origin: 10% 90%;
  margin-top: 3px;
}

/* End of Navbar
*/

/* start Content */
.page-header {
  padding: 0;
  position: relative;
  overflow: hidden;
  display: flex;
  align-items: center;
  background-size: cover;
  background-position: 50%;
}
.oblique-image{
  background-image: url('./curved11.jpg');
}
.oblique {
  transform: skewX(
-10deg
);
  overflow: hidden;
  width: 60%;
  right: -10rem;
  border-bottom-left-radius: .75rem;
}

.text-gradient.text-primary {
  background-image: linear-gradient(
310deg
,#7928ca,#ff0080);
color: #cb0c9f!important;
}
.text-gradient {
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  position: relative;
  z-index: 1;
}
h1{
  color:#344767;
}
p.lead{
  color: #67748e;
  font-weight: 400;
}
.buttons .btn{
  font-weight: 700;
  padding:.75rem 1.5rem;
  font-size: .75rem;
  border-radius: .5rem;
  text-transform: uppercase;
}
.buttons .Get-Started{
  color: aliceblue;
}
.buttons .Get-Started:hover{
  color: aliceblue;
}
.page-header .buttons .Read-more{
  color: #cb0c9f;
}

/* end of page-header */

/* start of section py-6 */
.py-6 {
  padding-top: 4rem!important;
  padding-bottom: 4rem!important;
}
.icon-shape {
  width: 48px;
  height: 48px;
  background-position: 50%;
  border-radius: .75rem;
}
.icon-shape i {
  color: #fff;
  opacity: .8;
  top: 11px;
  position: relative;
}
.bg-gradient-info {
  background-image: linear-gradient(
310deg
,#2152ff,#21d4fd);
}
.bg-gradient-warning {
  background-image: linear-gradient(
310deg
,#f53939,#fbcf33);
}

/* end of section py-6 */

/* start of section pt-5 */
.card.card-plain {
  background-color: transparent;
  box-shadow: none;
  border:none;
}
.border-radius-lg {
  border-radius: .75rem;
}
.card .card-body {
  font-family: Open Sans;
  padding: 1.5rem;
}
.text-gradient.text-dark {
  background-image: linear-gradient(
310deg
,#141727,#3a416f);
}
.card-body a{
  color: #344767;
  text-decoration: none;
}
.card-body a:hover{
  color: #344767;
  text-decoration: none;
}

button.btn-outline-pink{
  color: #cb0c9f;
  border-color: #cb0c9f;
  text-transform: uppercase;
  padding:.5rem 2rem;
  border-radius: .5rem;
}

/* end of section pt-5 */

/* start of footer */
.bg-gradient-dark {
  background-image: linear-gradient(
310deg
,#141727,#3a416f);
}
.opacity-6 {
  opacity: .6!important;
}
.font-weight-bolder {
  font-weight: 700!important;
}
.ms-n3 {
  margin-left: -1rem!important;
}
.font-weight-bold {
  font-weight: 600!important;
}
.text-lg {
  font-size: 1.125rem!important;
}
.opacity-5 {
  opacity: .5!important;
}